<template>
	<div class="createPanel">
		<div class="steps">
			<el-steps :active="step" finish-status="finish" simple>
				<el-step title="" icon="el-icon-setting"></el-step>
				<el-step title="IP" icon="el-icon-tickets"></el-step>
			</el-steps>
		</div>
		<div class="createCont o-h">
			<div class="createInfo f-r">
				<p class='title'></p>
				<div class="info">
					<div v-for='(item,key) in ruleForm'>
						<p class='row-info' v-show="(item||item===0)&&key!=='owner'&&key!=='name_suffix_begin'">
							<label>{{Chinesization[key]}}:</label>
							<span class='info-cont'>{{key==='image'?item[1]:item}}</span>
						</p>
						<p class='row-info' v-show="(item||item===0)&&key==='owner'">
							<label>{{Chinesization[key]}}:</label>
							<span class='info-cont'>{{item['label']}}</span>
						</p>
						<p class='row-info' v-show="item&&key==='name_suffix_begin'">
							<label>{{Chinesization[key]}}:</label>
							<span class='info-cont'>{{item}}</span>
						</p>
					</div>
					<div v-for='(item,key) in IPForm'>
						<p class='row-info' v-show="item&&key==='ippool_id'">
							<label>{{Chinesization[key]}}:</label>
							<span class='info-cont'>{{item}}</span>
						</p>
						<p class='row-info' v-show="item.length&&key==='ip_addr_set'">
							<label>{{Chinesization[key]}}:</label>
							<span class='info-cont'>{{item}}</span>
						</p>
					</div>
				</div>
				<div class="btns ta-c">
					<el-button type="primary" plain v-show='!isSetting' @click='backSetting'></el-button>
					<el-button type="success"plain v-show='isSetting' @click="submitForm('ruleForm')"></el-button>
					<el-button type="danger" plain v-show='!isSetting' @click="submitForm('IPForm')">
						<i class='fa fa-gear'></i>
						<span>&nbsp;</span>
					</el-button>
					<el-button type="info" plain @click='cancel'>&nbsp;</el-button>
				</div>
			</div>
			<div class="Settingform form f-l" v-show='isSetting'>
				<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" :size="$store.state.Size" label-position='left'>
					<el-form-item label="" prop="availability_zone">
						<el-radio-group v-model="ruleForm.availability_zone">
							<el-radio-button label=""></el-radio-button>
							<el-radio-button label=""></el-radio-button>
							<el-radio-button label="MIS-1"></el-radio-button>
						</el-radio-group>
					</el-form-item>
					<el-form-item label="CPU" prop="vcpus">
						<el-radio-group v-model.number="ruleForm.vcpus">
							<el-radio-button label="1">1</el-radio-button>
							<el-radio-button label="2">2</el-radio-button>
							<el-radio-button label="4">4</el-radio-button>
							<el-radio-button label="8">8</el-radio-button>
							<el-radio-button label="12">12</el-radio-button>
							<el-radio-button label="16">16</el-radio-button>
							<el-radio-button label="24">24</el-radio-button>
							<el-radio-button label="32">32</el-radio-button>
						</el-radio-group>
					</el-form-item>
					<el-form-item label="" prop="ram_mb">
						<el-radio-group v-model.number="ruleForm.ram_mb">
							<el-radio-button label="1">1GB</el-radio-button>
							<el-radio-button label="2">2GB</el-radio-button>
							<el-radio-button label="4">4GB</el-radio-button>
							<el-radio-button label="8">8GB</el-radio-button>
							<el-radio-button label="16">16GB</el-radio-button>
							<el-radio-button label="24">24GB</el-radio-button>
							<el-radio-button label="32">32GB</el-radio-button>
							<el-radio-button label="48">48GB</el-radio-button>
							<el-radio-button label="64">64GB</el-radio-button>
						</el-radio-group>
					</el-form-item>
					<el-form-item label="" prop="ephemeral_disk_gb">
						<el-input-number v-model="ruleForm.ephemeral_disk_gb" :min="0" :max="1500"></el-input-number>
						<span>GB</span>
					</el-form-item>
					<el-form-item label="" prop="image" size='medium'>
						<el-cascader :options="$store.state.osType" :show-all-levels="false" v-model="ruleForm.image" expand-trigger="hover" @change='changeOS' class='long-item'></el-cascader>
					</el-form-item>
					<el-form-item label="">
						<el-input placeholder="" v-model="ruleForm.ephemeral_disk" :readonly="true" style='width:200px'></el-input>
					</el-form-item>
					<el-form-item label="" prop="total">
						<el-input-number v-model="ruleForm.total" :min="1" :max="500" @change='alterTotal'></el-input-number>
					</el-form-item>
					<el-form-item label="" prop="owner">
						<el-select v-model="ruleForm.owner" filterable clearable :filter-method="ownerFilter" placeholder="" class='long-item'>
							<el-option v-for="item in ownerlist" :key="item.value" :label="item.label" :value="item">
								<span class='f-l'>{{ item.label }}</span>
      							<span class='f-r'style="color:#8492a6;font-size:13px">{{item.email.split('@')[0] }}</span>
							</el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="root" prop="adminPass">
						<el-input v-model.trim="ruleForm.adminPass" placeholder="root" class='long-item'></el-input>
						<el-button type="danger" @click='generatePass' plain></el-button>
					</el-form-item>
					<el-form-item label="">
						<el-input placeholder="" v-model.trim="ruleForm.name" style='width:200px'@change='changeName'></el-input>
					</el-form-item>
					<el-form-item label="" v-if='ruleForm.name'>
						<el-input-number v-model="ruleForm.name_suffix_begin" controls-position="right" :min='0'></el-input-number>
					</el-form-item>
					<el-form-item label="">
						<el-checkbox v-model="ruleForm.is_uneven"></el-checkbox>
					</el-form-item>
				</el-form>
			</div>
			<div class="Ipform form f-l" v-show='!isSetting'>
				<el-form :model="IPForm" :rules="IPrules" ref="IPForm" label-width="80px" :size="$store.state.Size" label-position='left'>
					<el-form-item label="" prop="netWork">
						<el-select v-model="IPForm.netWork" class='long-item' @change="changeNetwork" placeholder="">
							<el-option v-for="item in networkList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="IP" prop="ippool_id">
						<el-select v-model="IPForm.ippool_id" class='long-item' @change="changeIPpool" placeholder="IP">
							<el-option v-for="item in ipPool" :key="item.id" :label="item.availability_zone.name+' : '+item.first_ip+'-'+item.last_ip" :value="item.id">
							</el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="IP" prop="ips" v-if='isAvailable'>
						<div class='ips-label d-ib va-t'>
							<span class='Number'>({{ipInfo.len}})</span>
						</div>
						<div class='d-ib va-m ips-content' v-show='ipInfo.len'>
							<p v-for='item in ipInfo.items'>{{item}}</p>
						</div>
						<div class='d-ib va-m ips-content' v-show='!ipInfo.len'>
							<p>IP</p>
						</div>
					</el-form-item>
					<el-form-item label="" prop="total" v-if='isAvailable'>
						<el-input-number v-model="IPForm.total" :min="1" :max="500" :disabled="true"></el-input-number>
					</el-form-item>
					<el-collapse v-if='isAvailable&&ipInfo.len>0' style='width:600px'>
					  <el-collapse-item>
					    <template slot="title">
					      <i class="header-icon el-icon-info"></i>
					    </template>
					    <el-form :model="allocForm" :rules="allocRules" ref="allocForm" label-width="100px" :size="$store.state.Size" label-position='left'>
						    <el-form-item label="IP">
								<el-select v-model="allocForm.mode" class='long-item' placeholder="IP" @change='changeAllocType'>
									<el-option v-for="(value,key) in allocMode" :key="key" :label="value" :value="key">
									</el-option>
								</el-select>
							</el-form-item>
							<el-form-item label="" v-if="allocForm.mode==='ip_count'" prop='ip_count'>
								<el-input-number v-model="allocForm.ip_count" :min="1" :max="ipInfo.len" class='long-item'></el-input-number>
							</el-form-item>
							<el-form-item label="IP" v-if="allocForm.mode==='ip_addr'" prop='ip_addr'>
								<el-input v-model="allocForm.ip_addr" placeholder="IP" class='long-item'></el-input>
							</el-form-item>
							<el-form-item label="IP" v-if="allocForm.mode==='ip_range'" prop='ip_range'>
								<el-input v-model="allocForm.ip_range" placeholder="IP" class='long-item'></el-input>
							</el-form-item>
							<div class="alloc-btn ta-r">
								<el-button type="success" plain v-show='!isSetting' @click="submitForm('allocForm')">
									<i class='fa fa-wrench'></i>
									<span>IP</span>
								</el-button>
							</div>
							<el-transfer class='ip-transfer' filterable :filter-method="filterMethod" filter-placeholder="IP" v-model="allocatedIps" :titles="['IP', 'IP']" :data="iplist" @change='changeAllocated' v-if='isSelected'>
						    </el-transfer>
						</el-form>
					  </el-collapse-item>
					</el-collapse>
				</el-form>
				
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
		var validateIPtotal = (rule, value, callback) => {
			if (value > this.ipInfo.len) {
				callback(new Error('IP!IP'));
			} else {
				callback();
			}
		};
	    return {
	      	step:1,
	      	// Size:'small',//size
	      	Chinesization:{
	      		'availability_zone':'',
	      		'vcpus':'CPU()',
	      		'ram_mb':'(GB)',
	      		'image':'',
	      		'ephemeral_disk':'',
	      		'ephemeral_disk_gb':'(GB)',
	      		'name':'',
	      		'name_suffix_begin':'',
	      		'is_uneven':'',
	      		'total':'',
	      		'owner':'',
	      		'adminPass':'root',
	      		'ippool_id':'ipid',
	      		'ip_addr_set':'ip'
	      	},
	      	isSetting:true,//
      		// 
      		ownerlist:[],
      		owners:[],
      		// 
      		networkList:[],
      		// ip
      		ipPool:[],
      		// ip
      		ipInfo:{},
      		// IP
      		isAvailable:false,
      		// 1
	        ruleForm: {
	          availability_zone: '',//
	          vcpus: '1',
	          ram_mb:'1',//
	          ephemeral_disk_gb:0,//
	          image:['Rhel','rhel-6.8-server_0817'],//
	          ephemeral_disk:'Rhel 12GB',//
	          name:'',//
	          name_suffix_begin:0,
	          is_uneven:false,//
	          total:1,//
	          owner:'',//
	          adminPass:''//root
	        },
	        rules: {
	          availability_zone: [
	            { required: true, message: '', trigger: 'change' }
	          ],
	          vcpus: [
	            { required: true, message: 'cpu', trigger: 'change' }
	          ],
	          ram_mb: [
	            { required: true, message: '', trigger: 'change' }
	          ],
	          image: [
	            { required: true, message: '', trigger: 'change' }
	          ],
	          ephemeral_disk_gb: [
	            { required: true, message: '', trigger: 'change' }
	          ],
	          total: [
	            { required: true, message: '', trigger: 'change' }
	          ],
	          owner: [
	            { required: true, message: '', trigger: 'change' }
	          ],
	          adminPass: [
	            { required: true, message: '', trigger: 'change' },
	            { min: 6, max: 30,  message: '630'}
	          ]
	        },
	        // 2IP
		    IPForm:{
		    	netWork:'',//
		    	ippool_id:'',//ip
		    	ips:'',//ipip
		    	total:1,
		    	ip_addr_set:[]//ip
		    },
		    IPrules:{
		    	netWork: [
		            { required: true, message: '', trigger: 'change' }
		        ],
		        ippool_id: [
		            { required: true, message: 'ip', trigger: 'change' }
		        ],
		        total:[
		        	{ validator: validateIPtotal, trigger: 'change' }
		        ]
		    },
		    
		    // 3IP
		    allocMode:{
		    	ip_count:'',
		    	ip_addr:'',
		    	ip_range:''
		    },
		    allocForm:{
		    	mode:'ip_count',
		    	ip_count:1,
		    	ip_addr:'',
		    	ip_range:''
		    },
		    allocRules:{
		    	ip_count: [
		            { required: true, message: 'IP', trigger: 'change' }
		        ],
		        ip_addr: [ 
		            { required: true, message: 'IP', trigger: 'blur' },
		            { pattern: /^((25[0-5]|2[0-4]\d|[01]?\d\d?)($|(?!\.$)\.)){4}$/, message: 'IP' }
		        ],
		        ip_range: [
		            { required: true, message: 'IP', trigger: 'change' },
		            { pattern: /^((((25[0-5]|2[0-4]\d|1\d{2}|[1-9]\d|[0-9])\.){3}(25[0-5]|2[0-4]\d|1\d{2}|[1-9]\d|[0-9]))\-(((25[0-5]|2[0-4]\d|1\d{2}|[1-9]\d|[0-9])\.){3}(25[0-5]|2[0-4]\d|1\d{2}|[1-9]\d|[0-9])))$/, message: 'IP' }
		        ]
		    },
		    // 
		    filterMethod(query, item) {
				return item.value.indexOf(query) > -1;
			},
			// ip
			iplist:[],//ip
		    allocatedIps:[],//IP
		    isSelected:false,//ip
		    // 
		    messType:'success',
		    mess:''
	      };
	    },

	    created(){
	    	// 
	    	this.$http.get('/auth/users').then(res=>{
                // this.owners = res.body.data;
                res.body.data.forEach((item,i)=>{
                	this.owners.push({
                		'label':item.label,
                		'email':item.email,
                		'value':item.id
                	})
                	this.ownerlist = this.owners.slice(0);
                });
            });
	    },
	    methods: {
	    	//||
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						if(formName==='ruleForm'){
							this.isSetting = false;
							this.step = 2;
							// 
				            this.$http.get('/noec2/network').then(res=>{
				            	this.networkList = res.body.data;
				            })
						}else if(formName==='IPForm'){
							var createData = {};
							// 
							// ruleForm
							for(var key in this.ruleForm){
								if(key!=='phemeral_disk'){
									if(key==='image'){
										createData[key] = this.ruleForm[key][1];
									}else if(key==='owner'){
										createData[key] = this.ruleForm[key]['value'];
									}else if(key==='vcpus'){
										createData[key] = Number(this.ruleForm[key]);
									}else if(key==='ram_mb'){
										createData[key] = Number(this.ruleForm[key]);
									}else{
										createData[key] = this.ruleForm[key];
									}
								}
							}
							// IPForm
							for(var k in this.IPForm){
								if(k==='ippool_id'){
									createData[k] = this.IPForm[k];
								}else if(k==='ip_addr_set'){
									if(this.IPForm[k].length){
										createData[k] = this.IPForm[k];
									}
								}
							}

							// 
							const h = this.$createElement;
					        this.$msgbox({
					            title: '',
					            message: h('p', null, [
					                h('span', null, '')
					            // h('i', { style: 'color: teal' }, 'VNode')
					            ]),
					            showCancelButton: true,
					            showClose:false,
					            confirmButtonText: '',
					            cancelButtonText: '',
					            type: 'warning',
					            beforeClose: (action, instance, done) => {
				                 	if (action === 'confirm') {
					              	instance.confirmButtonLoading = true;
					                instance.confirmButtonText = '...';
					                this.$http.post("/noec2/instances",{ data:createData },
					                {
							            'headers': {
							                'Content-Type': 'x-www-form-urlencoded'
							            }
							        }).then(res=>{
										var status = res.body.result;
										console.log(res);
								        this.mess = '';
								        this.messType = 'success';
								        // loading
								        instance.confirmButtonLoading = false;
								        done();
									});
					            } else {
					                done();
					            }
					          }
					        }).then(action => {
					          	this.$alert(this.mess, '', {
					            	confirmButtonText: '',
					            	type: this.messType,
					            	showClose:false
					            });
					            this.$emit('cancel-create','success');//
					        }).catch(_=>{
					        	this.$message({
					        		type:'info',
					        		message:''
					        	});
					        });
							// alert('submit!');
						}else if(formName==='allocForm'){
							// IP
							// IP
							this.iplist = [];
					    	this.allocatedIps = [];
					    	this.IPForm.ip_addr_set = [];
					    	// 
							var mthd = this.allocForm.mode;
							var data = {
								'method':mthd,
								'ippool_id':this.IPForm.ippool_id
							}
							if(mthd==='ip_count'){
								data.ip_count = this.allocForm[mthd];
							}else if(mthd === 'ip_addr'){
								data.ip_addr = this.allocForm[mthd];
							}else{
								data.ip_range = this.allocForm[mthd];
							}
							// IP
							// ip
							const h = this.$createElement;
					        this.$msgbox({
					          title: '',
					          message: h('p', null, [
					            h('span', null, 'IP ')
					            // h('i', { style: 'color: teal' }, 'VNode')
					          ]),
					          showClose:false,
					          showCancelButton: true,
					          confirmButtonText: '',
					          cancelButtonText: '',
					          type: 'warning',
					          beforeClose: (action, instance, done) => {
					            if (action === 'confirm') {
					              instance.confirmButtonLoading = true;
					              instance.confirmButtonText = '...';
					              this.$http.get("/noec2/ippools/allcIP").then(
					              	res=>{
										var ipdata = res.body.data;
										ipdata.forEach((ip, index) => {
								          this.iplist.push({
								            label: ip,
								            key: index,
								            value: ip
								          });
								        });
								        this.mess = 'IP';
								        this.messType = 'success';
								        // 
								        this.isSelected = true;
								        // loading
								        instance.confirmButtonLoading = false;
								        done();
									});
					            } else {
					              done();
					            }
					          }
					        }).then(action => {
					          this.$message({
					            type: this.messType,
					            message: this.mess,
					            customClass:'pop-mess',
					            center:true
					          });
					        }).catch(_=>{
					        	this.$message({
					        		type:'info',
					        		message:''
					        	});
					        });
							
							// this.$http.post("/noec2/ippools"+this.IPForm.ippool_id+"",{
							// 	'method':mthd,
							// 	'ippool_id':this.IPForm.ippool_id,
							// 	mthd:this.allocForm[this.allocForm.mode]
							// },(res)=>{

							// })
						}
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			// 
			changeOS(v){
				this.ruleForm.ephemeral_disk = v[0]+' 12GB';
			},
			// 
			changeName(v){
				if(!v){
					this.ruleForm.name_suffix_begin = 0;
				}
			},
			// 
			alterTotal(v){
				this.IPForm.total = v;
			},
			// 
			ownerFilter(v){
				this.ownerlist = this.owners.filter((item)=>{
					return item.label.indexOf(v)>-1||item.email.indexOf(v)>-1;
				})
			},
			// 
			generatePass(){
				var length = 9,
	            charset = "abcdefghjkmnpqrstuvwxyzABCDEFGHJKMNPQRSTUVWXYZ23456789.*?&%+!@#$^=><",
	            retVal = "";
		        for (var i = 0, n = charset.length; i < length; ++i) {
		            retVal += charset.charAt(Math.floor(Math.random() * n));
		        }
		        this.ruleForm.adminPass = retVal;
			},
			// 
			backSetting(){
				this.isSetting = true;
				this.step = 1;
			},
			// 
			cancel(){
				this.$emit('cancel-create',false);
			},
			// IP
			// 
			changeNetwork(id){
				this.IPForm.ippool_id = '';
				this.isAvailable = false;
				// ip
				this.$http.get('/noec2/ippools').then(res=>{
					this.ipPool = res.body.data[id];
				});
			},
			// IP
			changeIPpool(id){
				this.isAvailable = false;
				// 
				// IP
				this.allocForm.mode = 'ip_count';
				this.allocForm.ip_count = 1;
				// IP
				this.iplist = [];
		    	this.allocatedIps = [];
		    	this.IPForm.ip_addr_set = [];
				this.$http.get('/noec2/ippools',{ params: { ippool_id:id } }).then(res=>{
					this.ipInfo = res.body.data;
					this.IPForm.ips = this.ipInfo.items;
					this.isAvailable = true;
				})
			},
			// 
			changeAllocType(){
				this.isSelected = false;
				// IP
				this.iplist = [];
		    	this.allocatedIps = [];
		    	this.IPForm.ip_addr_set = [];
			},
			// IP
			changeAllocated(){
				this.IPForm.ip_addr_set = [];
				for(var i=0;i<this.allocatedIps.length;i++){
					this.IPForm.ip_addr_set.push(this.iplist[this.allocatedIps[i]].value);
				}
			}
	    }
	}
</script>

<style scoped lang='less'>
	@import "~common/less/variable.less";
	.createPanel{
		.steps{
			padding: 10px;
			// border-bottom: 10px solid @colorBackground;
		}
		.createCont{
			padding: 15px 0 0 15px;
			.form{
				// padding-right: 360px;
				.long-item{
					width:360px;
				}
				.ips-label{
					color:#888;
					margin-right:6px;
					.Number{
						font-weight:700;
					}
				}
				.ips-content{
					width:300px;
					line-height: 24px;
					padding:5px;
					border:1px solid #67c23a;
					border-radius:4px;
				}
				.alloc-btn{
					width: 600px;
					padding-bottom: 20px;
					border-bottom: 1px solid #e0e0e0;
				}
				.ip-transfer{
					padding-top: 30px;
				}
			}
			.createInfo{
				margin-top: 15px;
				width: 25%;
				max-width: 300px;
				padding-left: 10px;
				border-left: 1px dotted #ccc;
				.title{
					line-height: 34px;
					font-size: 20px;
					border-bottom: 1px solid #b0b0b0;
				}
				.info{
					padding: 10px 5px;
					.row-info{
						line-height: 30px;
						font-size: 15px;
						.info-cont{
							font-size:17px;
							font-weight:700
						}
					}
				}
			}
		}
	}
</style>


