<template>
	<div class="instance">
		<div class="list" v-if='!isCreate'>
			<div class="btn-group">
				<!--  -->
				<el-button type="primary" class='f-l' icon="fa fa-plus" @click='createInstance' plain>&nbsp;</el-button>
				<el-button-group class='f-l operate-btns'>
					<el-button type="success" icon="fa fa-power-off" :disabled='!isEnabled' plain @click="operate('start',select_arr)">&nbsp;</el-button>
					<el-button type="warning" icon='fa fa-circle' :disabled='!isEnabled' plain @click="operate('shutdown',select_arr)">&nbsp;</el-button>
					<el-button type="danger" icon="fa fa-trash-o" :disabled='!isEnabled' plain @click="operate('delete',select_arr)">&nbsp;</el-button>
				</el-button-group>
				<el-dropdown class='f-l dorpdown-btn' @command="moreOperates">
				  <el-button type="info" icon='fa fa-cogs' :disabled='!isEnabled' plain>&nbsp;<i class="el-icon-arrow-down el-icon--right"></i>
				  </el-button>
				  <el-dropdown-menu slot="dropdown">
				  	<el-dropdown-item command='restart' class='restartItem'></el-dropdown-item>
				    <!-- <el-dropdown-item command='rebuild'></el-dropdown-item> -->
				  </el-dropdown-menu>
				</el-dropdown>
				<!--  -->
				<div class="table-btn f-r">
					<el-button icon="fa fa-refresh" title='' @click='refresh'></el-button>
					<el-button icon="fa fa-cog" title='' @click='settingColumn'></el-button>
				</div>
				<!--  -->
				<div class='search f-r p-r'>
					<input type="text" v-model="searchValue" @keyup.enter="$store.commit('handleSearch',searchValue)" @input="$store.commit('emptySearch',searchValue)" placeholder="">
					<el-button slot="append" icon="el-icon-search" @click="$store.commit('handleSearch',searchValue)" class='search-btn p-a' ></el-button>
				</div>
			</div>
			<!--  -->
			<div class="pagination ta-r">
				<el-pagination background :current-page="currentPage" :page-sizes="opts" :page-size="pageSize" :total="itemCount" @size-change="changePageSize" @current-change="changePage" layout="total, sizes, prev, pager, next, jumper">
			    </el-pagination>
			</div>
			<!--  -->
			<div class="table">
				<!-- @select='SelectItem' @select-all='selectAll'  -->
				<el-table border :data="CurrentData" @sort-change='sortChange' @selection-change="selectChange">
					<el-table-column type="selection" width='45' align='center'></el-table-column>
					<el-table-column v-if='setColumn.name' key='name' prop="name" label="" sortable='custom'>
						<template slot-scope='scope'>
							<el-button type='text'><i class='el-icon-refresh' title=''></i></el-button>
							<span>{{scope.row.name}}</span>
							
						</template>
					</el-table-column>
					<el-table-column v-if='setColumn.first_ip' key='first_ip' prop="first_ip" label="IP" sortable='custom'></el-table-column>
					<el-table-column v-if='setColumn.project' key='project' prop="project" label="" sortable='custom'></el-table-column>
					<el-table-column v-if='setColumn.created_by' key='created_by' prop="created_by" label="" sortable='custom'></el-table-column>
					<el-table-column v-if='setColumn.host' key='host' prop="host" label="" sortable='custom'></el-table-column>
					<el-table-column v-if='setColumn.power_state' key='power_state' prop="power_state" label="" sortable='custom'></el-table-column>
					<el-table-column v-if='setColumn.volumes' key='volumes'  label="">
						<template slot-scope="scope">
							<el-popover trigger="hover" placement="top">
								<p>: {{ scope.row.volumes }}</p>
								<div slot="reference">
									<el-tag size="medium" class='name-wrapper'>{{ scope.row.volumes }}</el-tag>
								</div>
							</el-popover>
						</template>
					</el-table-column>
					<el-table-column v-if='setColumn.image' prop='image' key='image' label="" sortable='custom'>
					</el-table-column>
					<el-table-column v-if='setColumn.status' key='status' prop="status" label="" sortable='custom'></el-table-column>
					<el-table-column v-if='setColumn.flavor' key='flavor' prop="flavor" label="" sortable='custom'></el-table-column>
					<el-table-column v-if='setColumn.availability_zone' key='availability_zone' prop="availability_zone" label="" sortable='custom'></el-table-column>
					<el-table-column v-if='setColumn.created' key='created' label="" sortable='custom'>
						<template slot-scope="scope">
					        <i class="el-icon-time"></i>
					        <span>{{ scope.row.created }}</span>
				        </template>
					</el-table-column>
					<el-table-column label="" width='100'>
						<template slot-scope="scope">
							<el-dropdown @command="singleOperates" @visible-change='changeVisible(scope.$index, scope.row)' placement='bottom'>
								<el-button type="danger" plain size='small'>
									<i class="el-icon-arrow-down el-icon--right"></i>
								</el-button>
								<el-dropdown-menu slot="dropdown">
									<el-dropdown-item command='console'><a href="/instances/a640a1c3-a56b-427e-9b2c-7821688a262b/console" target='_blank' style='color:#a4da89'></a></el-dropdown-item>
									<el-dropdown-item command='start'></el-dropdown-item>
									<el-dropdown-item command='shutdown'></el-dropdown-item>
									<el-dropdown-item command='delete' style='color:#f9a7a7'></el-dropdown-item>
									<el-dropdown-item command='restart'></el-dropdown-item>
									<el-dropdown-item command='rebuild'></el-dropdown-item>
				    				<el-dropdown-item command='edit'><i class='fa fa-edit'></i></el-dropdown-item>
								</el-dropdown-menu>
							</el-dropdown>
							<!-- <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button> -->
						</template>
					</el-table-column>
				</el-table>
			</div>
			<!--  -->
			<div class="pagination ta-r">
				<el-pagination background :current-page="currentPage" :page-sizes="opts" :page-size="pageSize" :total="itemCount" @size-change="changePageSize" @current-change="changePage" layout="total, sizes, prev, pager, next, jumper">
			    </el-pagination>
			</div>
		</div>
		<div class="createInstance" v-if='isCreate'>
			<create-instance @cancel-create='CancleCreate'></create-instance>
		</div>
		<!--  -->
		<el-dialog class='pop-dialog' :title="popTitle" :visible.sync="dialogVisible" width="30%" :show-close='false' center>
		  <div v-if='!isEdit'>
		  	<p class='text-dialog'><span>:</span><strong>{{objImage}}</strong></p>
		  	<el-cascader v-if='!isEdit' :options="$store.state.osType" :show-all-levels="false" v-model="image" expand-trigger="hover" style='width:100%' placeholder=''></el-cascader>
		  </div>
		  <div v-if='isEdit'>
			<el-form :model="editForm" :rules="editRules" ref="editForm" size='medium' label-width="100px" label-position='right'>
				<el-form-item label="">
					<el-select v-model="editForm.created_by" filterable clearable :filter-method="ownerFilter" placeholder="" class='dialog-item'>
						<el-option v-for="item in ownerlist" :key="item.value" :label="item.label" :value="item">
							<span class='f-l'>{{ item.label }}</span>
  							<span class='f-r'style="color:#8492a6;font-size:13px">{{item.email.split('@')[0] }}</span>
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="">
					<el-select v-model="editForm.status" placeholder="">
					    <el-option v-for="item in $store.state.Status" :key="item" :label="item" :value="item">
					    </el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop='name'>
					<el-input v-model.trim="editForm.name" placeholder="" class='dialog-item'></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-select v-model="editForm.contact" filterable clearable :filter-method="ownerFilter" placeholder="" class='dialog-item'>
						<el-option v-for="item in ownerlist" :key="item.value" :label="item.label" :value="item">
							<span class='f-l'>{{ item.label }}</span>
  							<span class='f-r'style="color:#8492a6;font-size:13px">{{item.email.split('@')[0] }}</span>
						</el-option>
					</el-select>
				</el-form-item>
			</el-form>
		  </div>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="dialogVisible = false"> </el-button>
		    <el-button v-if='!isEdit' type="primary" @click="submitDialog()"> </el-button>
		    <el-button v-if='isEdit' type="primary" @click="submitDialog('editForm')"> </el-button>
		  </span>
		</el-dialog>
		<!--  -->
		<el-dialog title="" :visible.sync="isSettingColumn" :show-close='false' width="30%">
			<div>
				<el-row :gutter="10" style='margin-bottom:15px'>
					<el-col :span="6"><el-checkbox v-model='temporaryList.name'></el-checkbox></el-col>
					<el-col :span="6"><el-checkbox v-model='temporaryList.first_ip'>IP</el-checkbox></el-col>
					<el-col :span="6"><el-checkbox v-model='temporaryList.project'></el-checkbox></el-col>
					<el-col :span="6"><el-checkbox v-model='temporaryList.created_by'></el-checkbox></el-col>
				</el-row>
				<el-row :gutter="10" style='margin-bottom:15px'>
					<el-col :span="6"><el-checkbox v-model='temporaryList.host'></el-checkbox></el-col>
					<el-col :span="6"><el-checkbox v-model='temporaryList.power_state'></el-checkbox></el-col>
					<el-col :span="6"><el-checkbox v-model='temporaryList.volumes'></el-checkbox></el-col>
					<el-col :span="6"><el-checkbox v-model='temporaryList.image'></el-checkbox></el-col>
				</el-row>
				<el-row :gutter="10">
					<el-col :span="6"><el-checkbox v-model='temporaryList.status'></el-checkbox></el-col>
					<el-col :span="6"><el-checkbox v-model='temporaryList.flavor'></el-checkbox></el-col>
					<el-col :span="6"><el-checkbox v-model='temporaryList.availability_zone'></el-checkbox></el-col>
					<el-col :span="6"><el-checkbox v-model='temporaryList.created'></el-checkbox></el-col>
				</el-row>
			</div>
			<span slot="footer" class="dialog-footer">
				<el-button @click="cancelColumn"> </el-button>
				<el-button type="primary" @click="confirmColumn"> </el-button>
			</span>
		</el-dialog>
	</div>
</template>
<script>
	import createInstance from 'components/cloudService/instance/createPanel';
	import {getInstanceList} from 'api/service';

	export default{
		components:{createInstance},
		data() {
	      return {
	      	isCreate:false,//
	      	searchValue:'',//
	      	isEnabled:false,//disabled
	        pageSize:25,//
            currentPage:1,//
            opts:[25,100,200,10000],
            Mess:'',
        	MessType:'success',
        	select_arr:[],//
        	len:0,//
        	popTitle:'',//
        	dialogVisible:false,//flag
        	isEdit:false,
        	
      		image:[],//
      		objArr:[],//
      		objImage:'',//
      		// 
      		owners:[],
      		ownerlist:[],

      		editForm:{
      			created_by:{value:'cbz',label:'',eamil:'bangzhongcao@sohu-inc.com'},
      			status:'',
      			name:'',
      			contact:{value:'zfx',label:'',eamil:'fengxiangzhang@sohu-inc.com'}
      		},
      		editRules:{
      			name:[
      				{ required: true, message: '', trigger: 'change' }
      			]
      		},
      		isSettingColumn:false,// 
      		// 
      		setColumn:{
      			name:true,
      			first_ip:true,
      			project:true,
      			created_by:false,
      			host:true,
      			power_state:true,
      			volumes:false,
      			image:true,
      			status:true,
      			flavor:true,
      			availability_zone:false,
      			created:true
      		},
      		temporaryList:{
      			
      		}// 
	      }
	    },
	    computed:{
	    	CurrentData(){
                var _start = ( this.currentPage - 1 ) * this.pageSize;
                var _end = this.currentPage * this.pageSize;
                if(this.$store.state.tableData.length<_end){
                    return this.$store.state.tableData.slice(_start);
                }else{
                    return this.$store.state.tableData.slice(_start,_end);
                }
                
            },
            itemCount(){
                return this.$store.state.tableData.length;
            }
	    },
	    created(){
	    	// 
	    	this.$http.get('/noec2/instances').then(res=>{
                var list = res.body.data;
                this.$store.commit('changeData',list);
                // this.loading = false;

                // 
		    	this.$http.get('/auth/users').then(res=>{
	                // this.owners = res.body.data;
	                res.body.data.forEach((item,i)=>{
	                	this.owners.push({
	                		'label':item.label,
	                		'email':item.email,
	                		'value':item.id
	                	})
	                	this.ownerlist = this.owners.slice(0);
	                });
	            });
            })
	    },
	    methods: {
            // 
	    	sortChange(obj){
	    		// objnull
	    		this.$store.commit('sortChange',obj);
	    	},
	    	// 
            changePage(index){
                this.currentPage = index;
                window.scrollTo(0, 0);
            },
            // 
            changePageSize(size){
                this.pageSize = size;
                this.currentPage = 1;
                window.scrollTo(0, 0);
            },
            selectChange(selection){
            	this.select_arr = selection;
            	this.len = selection.length;
            	if(this.len){
            		this.isEnabled = true;
            	}else{
            		this.isEnabled = false;
            	}
            },
			formatter(row, column) {
				return row.address;
			},
			// 
			changeVisible(index,obj){
				this.objArr = [];
				this.objArr.push(obj);
				// 
				this.objImage = obj.image;
			},
			// 
			moreOperates(command){
				if(command==='restart'){
					this.operate(command,this.select_arr);
				}
			},
			// 
			singleOperates(command){
				// 
				if(command==='rebuild'){
					// image
					this.image = [];
					// 
					this.isEdit = false;
					// 
					this.popTitle = '';
					this.dialogVisible = true;
				} else if(command==='edit'){
					// 
					this.isEdit = true;
					// 
					// this.editForm.status = this.objArr[0].status;
					this.editForm.status = 'ACTIVE';
					this.editForm.name = this.objArr[0].name;
					// 
					this.popTitle = '';
					this.dialogVisible = true;
				} else if(command==='console'){
					console.log(command);
				} else {
					this.operate(command,this.objArr);
				}
			},
			// 
			ownerFilter(v){
				this.ownerlist = this.owners.filter((item)=>{
					return item.label.indexOf(v)>-1||item.email.indexOf(v)>-1;
				})
			},
			//  /
			submitDialog(formName){
				if(formName){
					this.$refs[formName].validate((valid) => {
						if (valid) {
							// 
							var editData = {};
							for(var key in this.editForm){
								if(key==='created_by'||key==='contact'){
									editData[key] = this.editForm[key].value;
								}else{
									editData[key] = this.editForm[key];
								}
							}
							// 
							this.$http.patch('/noec2/instances/1001',{data:editData}).then(res=>{
								// 
								this.$http.get('/noec2/instances').then(res=>{
					                // this.$store.state.listData = this.initData(res.body.data);
					                this.$store.state.listData = res.body.data;
					                this.$store.state.tableData = this.$store.state.listData.slice(0);
					                this.dialogVisible = false;
					            });
								
							})
						}
					});
				}else{
					if(this.image.length){
						// 
						this.objArr[0].image = this.image[1];
						this.dialogVisible = false;
					}
				}
			},
			// ,
            refresh(){
                this.$http.get('/noec2/instances').then(res=>{
                    // this.$store.state.listData = this.initData(res.body.data);
                    this.searchValue = '';//
                    this.currentPage = 1;//1
                    //
					var list = res.body.data;
                	this.$store.commit('changeData',list);
                });
            },
            // 
            // 
            settingColumn(){
            	for(var key in this.setColumn){
            		this.$set(this.temporaryList,key,this.setColumn[key]);
            	}
            	this.isSettingColumn = true;
            },
            // 
            cancelColumn(){
            	this.isSettingColumn = false;
            	this.temporaryList = {};
            	for(var key in this.setColumn){
            		this.$set(this.temporaryList,key,this.setColumn[key]);
            	}
            },
            // 
            confirmColumn(){
            	this.isSettingColumn = false;
            	this.setColumn = {};
            	for(var key in this.temporaryList){
            		this.$set(this.setColumn,key,this.temporaryList[key]);
            	}
            },
            // 
            createInstance(){
            	this.isCreate = true;
            },
            // 
            CancleCreate(flag){
            	if(flag==='success'){
            		this.refresh();
            	}
            	this.isCreate = false;
            },
            //  ///
            operate(actions,selectArr){
            	// 
            	var instance_action = '';
            	var color = '';
            	if(actions==='start'){
            		instance_action = '';
            		color = '#67C23A';
            	}else if(actions==='shutdown'){
            		instance_action = '';
            		color = '#909399';
            	}else if(actions==='delete'){
            		instance_action = '()';
            		color = '#F56C6C';
            	}else if(actions==='restart'){
            		instance_action = '';
            		color = '#E6A23C';
            	}
            	// 
            	const h = this.$createElement;
            	// 
            	var instance_names = [];
            	for(var i in selectArr){
            		instance_names.push(h('span',{ style: 'color: teal;margin-right:10px' },selectArr[i].name));	
            	}
				// 
		        this.$msgbox({
		            title: '',
		            message: h('p', null, [
		                h('span', null, ''),
		            	h('p', {style:'word-wrap: break-word;overflow: hidden'}, instance_names),
		            	h('span', null, ''),
		            	h('strong', { style: "margin-left:5px;font-size:15px;color: "+color+"" }, instance_action)
		            ]),
		            showCancelButton: true,
		            showClose:false,
		            confirmButtonText: '',
		            cancelButtonText: '',
		            type: 'warning',
		            beforeClose: (action, instance, done) => {
	                 	if (action === 'confirm') {
			              	instance.confirmButtonLoading = true;
			                instance.confirmButtonText = '...';
			                if(this.len<=1){
			                	// 
			                	var startData = {};
			                	
			                	if(actions==='delete'){
			                		// ajax
				                	this.$http.delete("/noec2/instances/1001").then(res=>{
										var status = res.body.result;
								        this.mess = ''+instance_action+'';
								        this.messType = 'success';
								        // loading
								        instance.confirmButtonLoading = false;
								        done();
									});
			                	}else{
			                		var startData = {'action':actions};
			                		// ajax
				                	this.$http.post("/noec2/instances/1001",{ data:startData }).then(res=>{
										var status = res.body.result;
								        this.mess = ''+instance_action+'';
								        this.messType = 'success';
								        // loading
								        instance.confirmButtonLoading = false;
								        done();
									});
			                	}
			                	
			                }else{
			                	// 
			                	var startBatchData = {'action':'start','object_ids':[]};
			                	for(var i=0;i<selectArr.length;i++){
			                		startBatchData['object_ids'].push(selectArr[i]['id'])
			                	}
			                	this.$http.post("/noec2/instances/batch/",{ data:startBatchData }).then(res=>{
									var status = res.body.result;
							        this.mess = ''+instance_action+'';
							        this.messType = 'success';
							        // loading
							        instance.confirmButtonLoading = false;
							        done();
								});
			                }
			            } else {
			                done();
			            }
		        	}
		        }).then(action => {
		          	this.$alert(this.mess, '', {
		            	confirmButtonText: '',
		            	type: this.messType,
		            	showClose:false
		            });
		        }).catch(() => {
						this.$message({
						type: 'info',
						customClass:'pop-mess',
						message: '',
						duration: 700
					});          
				});
            }
	    }
	}
</script>

<style scoped lang='less'>
	@import "~common/less/variable.less";
	.instance{
		padding: 10px 15px;
		.list{
			.btn-group{
				padding: 10px 0;
				overflow: hidden;
				border-bottom: 10px solid @colorBackground;
				.operate-btns{
					margin-left: 30px;
				}
				.dorpdown-btn{
					margin-left: 20px;
				}
				.table-btn{
					.el-button{
						font-size: 18px;
						border: 0;
						margin-left: 0;
						padding:12px;
					}
					
				}
				.search{
					width: 20%;
					max-width: 320px;
					margin-right: 20px;
					padding-right:56px;
					input{
						width: 100%;
						height: 38px;
						padding: 0 8px;
						border: 1px solid #dcdfe6;
						border-radius: 3px;
						font-size: 14px;
						line-height: 38px;
						&::-webkit-input-placeholder{
							color:#d0d0d0;
						}
					}
					.search-btn{
						top:0;
						right: 0;
						border-radius:  0 3px 3px 0;
					}
				}
			}
			.table{
				.name-wrapper{
					width:100%; 
					line-height: 20px;
					white-space:pre-wrap;
					overflow: hidden;
					height:auto
				}
			}
			.pagination{
				padding: 10px 0;
			}
		}
		.pop-dialog{
			.text-dialog{
				margin-bottom: 8px;
			}
			.dialog-item{
				width: 90%;
			}
		}
		
	}
</style>


