<template>
	<div class="createHostGroups">
		<el-form class='HostGroupsForm' :model="HostGroupsForm" :rules="HostGroupsRules" ref="HostGroupsForm" label-width="100px">
			<el-form-item label="" prop='name'>
				<el-input class='group-name' placeholder="" v-model.trim="HostGroupsForm.name"></el-input>
				<el-button v-if='!isAlter' type="primary" plain class='createHostsBtn' @click="createHosts('HostGroupsForm')"></el-button>
				<el-button v-if='isAlter' type="success" plain round class='createHostsBtn' @click="createHosts('HostGroupsForm')"></el-button>
			</el-form-item>
			<el-form-item class='transfer'>
				<el-transfer filterable filter-placeholder="IP" v-model="instances" :titles="['', '']" :data="list">
    			</el-transfer>
			</el-form-item>
		</el-form>
		
	</div>
</template>

<script type="text/javascript">
	export default{
		props:['instanceList','isAlter','hostsgroupInfo'],
		data(){
			return{
				instances:[],//
				list:[],//
				hostId:'',//id
				HostGroupsForm:{
					name:''
				},
				HostGroupsRules:{
					name:[
						{ required: true, message: '', trigger: 'change' }
					]
				}
			}
		},
		mounted(){
			// 
			this.instanceList.forEach((item,index)=>{
				var labels = item.first_ip+' ('+item.name+')';
				this.list.push({
					label:labels,
					key:item.first_ip
				});
			});
			// 
			if(this.hostsgroupInfo.hosts){
				this.hostId = this.hostsgroupInfo.hostgroup.id;
				this.HostGroupsForm.name = this.hostsgroupInfo.hostgroup.name;
				this.instances = this.hostsgroupInfo.hosts.slice(0);
			}
		},
		methods:{
			popMess(obj){
				const h = this.$createElement;
				var _this = this;
				console.log(obj.data);
		        this.$msgbox({
		          title: '',
		          message: h('p', null, [
		            h('span', null, obj.tip),
		            h('i', { style: 'color: teal' }, this.HostGroupsForm.name)
		          ]),
		          showClose:false,
		          showCancelButton: true,
		          confirmButtonText: '',
		          cancelButtonText: '',
		          type: 'warning',
		          beforeClose: (action, instance, done) => {
		            if (action === 'confirm') {
						instance.confirmButtonLoading = true;
						instance.confirmButtonText = '...';
						var ajaxFunc;
						if(obj.operate==='edit'){
							ajaxFunc = function(){
								return _this.$http.put(obj.url,obj.data);
							}
						}else if(obj.operate==='create'){
							ajaxFunc = function(){
								return _this.$http.post(obj.url,obj.data);
							}
						}
						ajaxFunc().then(res=>{
							this.$emit('create-hosts',res.body.data);
							this.mess = obj.mess;
							this.messType = 'success';
							// loading
							instance.confirmButtonLoading = false;
							done();
						});
		            } else {
		              	done();
		            }
		          }
		        }).then(action => {
		          this.$message({
		            type: this.messType,
		            message: this.mess,
		            customClass:'pop-mess',
		            center:true
		          });
		        }).catch(_=>{
		        	this.$message({
		        		type: 'info',
		        		message:obj.cancelTip
		        	})
		        });
			},
			// 
			createHosts(formName){
				this.$refs[formName].validate((valid) => {
					if (valid) {
						// ip
						if(this.isAlter){
							var obj={
								operate:'edit',
								// url:'/monitor/hostgroups/'+this.hostId+'/',
								url:'/monitor/hostgroups/1/',
								data:{"name":this.HostGroupsForm.name,"hosts":this.instances},
								tip:'',
								mess:'',
								cancelTip:''
							};
							this.popMess(obj);
						}else{
							var obj={
								operate:'create',
								// url:'/monitor/hostgroups/'+this.hostId+'/',
								url:'/monitor/hostgroups/',
								data:{"name":this.HostGroupsForm.name,"hosts":this.instances},
								tip:'',
								mess:'',
								cancelTip:''
							};
							this.popMess(obj);
						}
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			}
		}
	}
</script>
<style lang='less'>
	.createHostGroups{
		.HostGroupsForm{
			.group-name{
				width: 50%;
				max-width: 400px;
				min-width: 350px;
			}
			.createHostsBtn{
				margin-left: 30px;
			}
			.transfer{
				margin-top: 25px;
				.el-transfer-panel{
					width: 250px;
					.el-transfer-panel__body{
						height: 323px;
						.el-transfer-panel__list{
							height:273px;
						}
					}
				}
			}
		}
		
	}
</style>


